<template>
  <header id="page-topbar">
    <div class="navbar-header">
      <div class="d-flex">
        <!-- LOGO -->
        <div class="navbar-brand-box">
          <a href="/home" class="logo logo-dark">
            <span class="logo-sm">
              <img src="@/assets/images/logo-sm-dark.png" alt height="22" />
            </span>
            <span class="logo-lg">
              <img src="@/assets/images/logo-dark.png" alt height="20" />
            </span>
          </a>

          <a href="/home" class="logo logo-light">
            <span class="logo-sm">
              <img src="@/assets/images/logo-sm-light.png" alt height="22" />
            </span>
            <span class="logo-lg">
              <img src="@/assets/images/logo-light.png" alt height="20" />
            </span>
          </a>
        </div>

        <button
          @click="toggleMenu"
          type="button"
          class="btn btn-sm px-3 font-size-24 header-item waves-effect"
          id="vertical-menu-btn"
        >
          <i class="ri-menu-2-line align-middle"></i>
        </button>
        
      </div>

      <div class="d-flex">
        <div class="dropdown d-inline-block d-lg-none ml-2">
          <button
            type="button"
            class="btn header-item noti-icon waves-effect"
            id="page-header-search-dropdown"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false"
          >
            <i class="ri-search-line"></i>
          </button>
          <div
            class="dropdown-menu dropdown-menu-lg dropdown-menu-right p-0"
            aria-labelledby="page-header-search-dropdown"
          >
            <form class="p-3">
              <div class="form-group m-0">
                <div class="input-group">
                  <input type="text" class="form-control" placeholder="Search ..." />
                  <div class="input-group-append">
                    <button class="btn btn-primary" type="submit">
                      <i class="ri-search-line"></i>
                    </button>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>

        <b-dropdown
          class="d-none d-lg-inline-block d-lg-inline-block ml-1"
          menu-class="dropdown-menu-lg"
          right
          toggle-class="header-item noti-icon"
          variant="black"
        >
          <template v-slot:button-content>
           {{usuarioDB.rol}}
          </template>
        </b-dropdown>

        <div class="dropdown d-none d-lg-inline-block ml-1">
          <button
            type="button"
            class="btn header-item noti-icon waves-effect"
            @click="initFullScreen"
          >
            <i class="ri-fullscreen-line"></i>
          </button>
        </div>

        <b-dropdown
          right
          menu-class="dropdown-menu-lg p-0"
          toggle-class="header-item noti-icon"
          variant="black"
        >
          <template v-slot:button-content>
            <i class="ri-notification-3-line"></i>
            <span class="noti-dot" v-if="notificaciones.length>0"></span>
          </template>
          <div class="p-3">
            <div class="row align-items-center">
              <div class="col">
                <h6 class="m-0">{{ $t('navbar.dropdown.notification.text')}}</h6>
              </div>
              <div class="col-auto">
                <a href="#!" class="small">{{ $t('navbar.dropdown.notification.subtext')}}</a>
              </div>
            </div>
          </div>
          <simplebar style="max-height: 250px;">
            <a v-for="(msg, index) in notificaciones" :key="index" :href="msg.modulo" class="text-reset notification-item">
              <div class="media">
                <div class="avatar-xs mr-3">
                  <span :class="msg.color">
                    <i :class="msg.icon"></i>
                  </span>
                </div>
                <div class="media-body">
                  <h6 class="mt-0 mb-1">{{msg.titulo}}</h6>
                  <div class="font-size-12 text-muted">
                    <p class="mb-1">{{msg.descripcion}}</p>
                    <p class="mb-0">
                     Remitente: {{msg.remitente.nombre }} {{msg.remitente.apellido }}
    
                    </p>
                    <p class="mb-0">
                      <i class="mdi mdi-clock-outline"></i>
                      {{msg.created_at | capitalize }}
                    </p>
                  </div>
                </div>
              </div>
            </a>
          </simplebar>
          <div class="p-2 border-top">
            <a
              class="btn btn-sm btn-link font-size-14 btn-block text-center"
              href="/notificaciones"
            >
              <i class="mdi mdi-arrow-right-circle mr-1"></i>
              {{ $t('navbar.dropdown.notification.button')}}
            </a>
          </div>
        </b-dropdown>

        <b-dropdown
          right
          variant="black"
          toggle-class="header-item"
          class="d-inline-block user-dropdown"
        >
          <template v-slot:button-content>
            
            <img
              class="rounded-circle header-profile-user"
              :src="usuarioDB.imagen"
              alt="Header Avatar"
            />
            <span class="d-none d-xl-inline-block ml-1">{{usuarioDB.nombre}} {{usuarioDB.apellido}}</span>
            <i class="mdi mdi-chevron-down d-none d-xl-inline-block"></i>
          </template>
          <!-- item-->
          <a class="dropdown-item" href="/perfil">
            <i class="ri-user-line align-middle mr-1"></i>
            Perfil
          </a>
          <div class="dropdown-divider"></div>
          <a class="dropdown-item text-danger"  @click="salir()">
            <i class="ri-shut-down-line align-middle mr-1 text-danger"></i>
            {{ $t('navbar.dropdown.kevin.list.logout') }}
          </a>
        </b-dropdown>

     
      </div>

  <notifications group="foo" />
    </div>
  </header>
</template>
<script>
import push from 'push.js'
import io from 'socket.io-client';
import simplebar from "simplebar-vue";
import i18n from "../i18n";
import {mapState,mapMutations, mapActions} from 'vuex'
import moment from 'moment';
export default {
  data() {
    return {
      polling: null,
      socket : io('https://pristelapp.herokuapp.com/'),
      canal: '',
      user: '',
      message: '',
      messages: [],
      notificaciones: [],
      alertas: [],
      permisos: [],
      languages: [
      ],
      current_language: "en"
    };
  },
  components: { simplebar, push },
  computed:{
    ...mapState(['usuarioDB'])
 },
      filters: {
        capitalize: function (value) {
          if (!value) return ''
          let fecha = moment();
            let dias =fecha.diff(value, 'days'); 
            let horas =fecha.diff(value, 'hours'); 
            let minutos =fecha.diff(value, 'minutes'); 
          if (dias>1) {
           return "hace " + dias + " dias."; 
          } if (dias== 0 && horas>1) {
               return "hace " + horas + " horas."; 
          } else {
               return "hace " + minutos + " minutos."; 
          }

        }
    },
  methods: {
    selector(){
      if (this.usuarioDB.rol==='Administrador') {
          this.alertasAdministradorAth();
      }
      if(this.usuarioDB.rol==='Coordinador'){
        this.alertasCoordinadorAth();
      }
      if(this.usuarioDB.rol==='Analista'){
           this.alertasAnalistasAth();
      }
      if(this.usuarioDB.rol==='Tecnico'){
           this.alertasTecnicosAth();
      }
    },
    sendMessage(e) {
        e.preventDefault();
        //console.log(this.socket.id);
        this.socket.emit('servidor', {
            user: this.user,
            message: this.message,
            usuario: this.socket.id
        });
        this.message = ''
    }, 
    ...mapActions(['cerrarSession','cargar']),
     salir(){
      localStorage.removeItem('token');
      this.$router.push({ name: '/' });
      this.cerrarSession();
     },
    toggleMenu() {
      this.$parent.toggleMenu();
    },

