                    let value= this.alertas[index].fecha_vencimiento;
                    this.permisos=this.alertas[index].cajero_ath.entidad.permiso_coordinadors;
                    let existe = this.permisos.some(permiso=>permiso.uid=== this.usuarioDB.id);
                    if (existe) {
                      let prioridad = this.alertas[index].prioridad;
                      if (prioridad==='Critico') {
                        let fecha = moment();
                        let restan = fecha.diff(value, 'hours'); 
                        restan = restan*-1;
                        let limite = this.alertas[index].cajero_ath.ciudad.alerta_coordinador_critico;
                        limite=limite*1;
                        if (restan<0) {
                          push.create(`ATH-${this.alertas[index].id}`, {
                              body: `esta llamada tiene ${restan*-1} horas vencida`,
                              icon: '/icon.png',
                              timeout: 4000,
                              onClick: function () {
                                  window.focus();
                                  this.close();
                              }
                          });
                        }
                        if (limite>=restan) {
                            push.create(`ATH-${this.alertas[index].id}`, {
                              body: `esta llamada le restan  ${restan} horas  para expirar`,
                              icon: '/icon.png',
                              timeout: 4000,
                              onClick: function () {
