                        restan = restan*-1;
                        let limite = this.alertas[index].cajero_ath.ciudad.alerta_coordinador_bajo;
                        limite=limite*1;
                        if (restan<0) {
                          push.create(`ATH-${this.alertas[index].id}`, {
