                              icon: '/icon.png',
                              timeout: 4000,
                              onClick: function () {
                                  window.focus();
                                  this.close();
                              }
                          });
                        }
                      }
                      if (prioridad==='Medio') {
                        let fecha = moment();
                        let restan = fecha.diff(value, 'hours'); 
                        restan = restan*-1;
                        let limite = this.alertas[index].cajero_ath.ciudad.alerta_tecnico_mediano;
                        console.log(limite);
                        console.log(restan);
                        limite=limite*1;
                        if (restan<0) {
                          push.create(`ATH-${this.alertas[index].id}`, {
                              body: `esta llamada tiene ${restan*-1} horas vencida`,
                              icon: '/icon.png',
                              timeout: 4000,
                              onClick: function () {
                                  window.focus();
                                  this.close();
                              }
                          });
                        }
                        if (limite>=restan) {
                            push.create(`ATH-${this.alertas[index].id}`, {
                              body: `esta llamada le restan  ${restan} horas  para expirar s`,
                              icon: '/icon.png',
                              timeout: 4000,
                              onClick: function () {
                                  window.focus();
                                  this.close();
                              }
                          });
                        }
                      }
                      if (prioridad==='Bajo') {
                        let fecha = moment();
                        let restan = fecha.diff(value, 'hours'); 
                        restan = restan*-1;
                        let limite = this.alertas[index].cajero_ath.ciudad.alerta_tecnico_bajo;
                        console.log(limite);
                        console.log(restan);
                        limite=limite*1;
                        if (restan<0) {
                          push.create(`ATH-${this.alertas[index].id}`, {
                              body: `esta llamada tiene ${restan*-1} horas vencida`,
                              icon: '/icon.png',
                              timeout: 4000,
                              onClick: function () {
                                  window.focus();
                                  this.close();
                              }
                          });
                        }
                        if (limite>=restan) {
                            push.create(`ATH-${this.alertas[index].id}`, {
