                              icon: '/icon.png',
                              timeout: 4000,
                              onClick: function () {
                                  window.focus();
                                  this.close();
                              }
                          });
                        }
                      }
                  }
                }

              }).catch(e => { 
          });
       }, 10000000)
      },      
    initFullScreen() {
      document.body.classList.toggle("fullscreen-enable");
      if (
        !document.fullscreenElement &&
        /* alternative standard method */ !document.mozFullScreenElement &&
        !document.webkitFullscreenElement
      ) {
        // current working methods
        if (document.documentElement.requestFullscreen) {
          document.documentElement.requestFullscreen();
        } else if (document.documentElement.mozRequestFullScreen) {
          document.documentElement.mozRequestFullScreen();
        } else if (document.documentElement.webkitRequestFullscreen) {
          document.documentElement.webkitRequestFullscreen(
            Element.ALLOW_KEYBOARD_INPUT
          );
        }
      } else {
        if (document.cancelFullScreen) {
          document.cancelFullScreen();
        } else if (document.mozCancelFullScreen) {
          document.mozCancelFullScreen();
        } else if (document.webkitCancelFullScreen) {
          document.webkitCancelFullScreen();
        }
      }
    },
    toggleRightSidebar() {
      this.$parent.toggleRightSidebar();
    },
    setLanguage(locale) {
      i18n.locale = locale;
      this.current_language = i18n.locale;
    },
    async chanel(index){
      let data = new FormData();
      data.append("canal",index);
      await this.axios.put('api/user/canal', data).then(response => {
        console.log(response);
        if (response.status==200) {

          }
        }).catch(e => { 
          if (e.response.status==403) {
            localStorage.removeItem('token');
            this.$router.push({ path: '/' });
            this.cerrarSession();
          }
    });
    },
    async listarNotificaciones(index){
      await this.axios.get('api/notificacion/').then(response => {
        if (response.status==200) {
            this.notificaciones=response.data.rows;
          }

        }).catch(e => { 
      });
    },

  },
  beforeDestroy () {
	    clearInterval(this.polling)
  },
  created(){
    this.selector();
      this.listarNotificaciones();
      this.socket.on('connect', () => {
        this.chanel(this.socket.id)
      });
      this.socket.on('cliente', (data) => {
        console.log(data);
        this.listarNotificaciones();
        this.$notify({
          group: 'foo',
          title: 'Nueva notificacion',
          text: 'Hola tienes una nueva notificacion!'
        });
        push.create(`${data.titulo}`, {
              body: `${data.descripcion}`,
              icon: '/icon.png',
              timeout: 4000,
              onClick: function () {
                  window.focus();
                  this.close();
              }
          });
        this.cargar();
      });
      this.socket.on('chat', (data) => {
        console.log(this.usuarioDB.id);
         console.log(data);
        if (data==this.usuarioDB.id) {
          this.cargar();
       }else{
        this.$notify({
          group: 'foo',
          title: 'Nuevo chat',
          text: 'Hola tienes una nueva notificacion!'
        });
        this.cargar();
       }
        this.listarNotificaciones();
      
        
      });
  }
};
